@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Poppins:wght@400;500;600;700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
}

.col-4 h4 {
    color: #555;
    font-weight: 500;
    font-size: 18px;
    margin-bottom: 15px;
}

.col-4 p {
    font-size: 20px;
    color: #000;
    font-weight: 500;
}

.product {
    border: 1px solid lightgray;
    border-radius: 10px;
}

.product:hover {
    transform: translateY(-5px);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
/*
===========================
Products data styling
===========================
*/

.col-4 {
  flex-basis: 25%;
  padding: 15px;
  min-width: 270px;
  margin-bottom: 50px;
  transition: transform 0.5s ease;
}

.row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.products {
  background-color: #fff;
}

.small-container {
  max-width: 1300px;
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
}

.col-4:hover {
  transform: translateY(-5px);
  cursor: pointer;
}

.col-4 img {
  width: 100%;
}

.rating {
  display: flex;
}

.rating svg {
  fill: orangered;
}

.title {
  text-align: center;
  margin: 0 auto 80px;
  position: relative;
  line-height: 60px;
  color: #333;
  font-size: 22px;
  font-weight: 600;
}

.title:after {
  content: "";
  background-color: crimson;
  width: 80px;
  height: 5px;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}



/*
=====================
Offer section styling
=====================
*/

.offer {
    background-color: #f6d327;
    background-image: linear-gradient(315deg, #f6d327 0%, #de4daa 74%);
    margin-top: 80px;
    padding: 30px 0;
}

.small-container {
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.col-2 {
    flex-basis: 50%;
    min-width: 300px;
}

@media screen and (max-width: 960px){
    .col-2 {
        flex-basis: 100%;
    }
}

.col-2 img {
    max-width: 100%;
    padding: 60px 0;
    margin-top: 250px;
}

.small-container{
    max-width: 1300px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

.offer .offer-image {
    height: 600px;
    width: 100%;
    background-image: url(/react-sport-store/static/media/nike-sunglasses1.03c63694.jpg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    transition: 1s ease;

    animation-name: animate;
    animation-direction: alternate-reverse;
    animation-duration: 15s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: ease-in-out;
}

@keyframes animate {
    0% {
        background-image: url(/react-sport-store/static/media/nike-sunglasses1.03c63694.jpg);
    }

    25% {
        background-image: url(/react-sport-store/static/media/nike-sunglasses2.3ba2c478.jpg);
    }

    50% {
        background-image: url(/react-sport-store/static/media/nike-sunglasses3.888aabf7.jpg);
    }

    75% {
        background-image: url(/react-sport-store/static/media/nike-sunglasses4.81309d86.jpg);
    }

    100% {
        background-image: url(/react-sport-store/static/media/nike-sunglasses5.985db5d5.jpg);
    }
}

@media screen and (max-width: 960px){
    .offer .offer-image {
        height: 400px;
    }
}

@media screen and (max-width: 300px) {
    .offer .offer-image {
        height: 200px;
    }
}

.offer .col-2 p {
    color: #36454F;
    font-weight: 600;
}

/* @media screen and (max-width: ) */

.offer .col-2 small {
    color: #fff;
    font-size: 20px;
    line-height: 35px;
}

@media screen and (max-width: 300px) {
    .offer .col-2 small {
        color: #fff;
        font-size: 15px;
        line-height: 25px;
    }
}

.offer .small-container .col-2 {
    margin-top: 30px;
}

.offer .offer-btn {
    padding: 8px 30px;
    background: #000;
    color: #fff;
    font-weight: 500;
    border: 1px solid #fff;
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 30px;
    transition: background 0.3s ease;
    text-decoration: none;
    margin-top: 10px;
}

.offer .offer-btn:hover {
    background: rgba(0, 0, 0, 0.2);
}

.col-2 h1 {
    font-size: 45px;
    line-height: 70px;
    margin: 35px 0;
    color: #fff;
}

@media screen and (max-width: 300px) {
    .col-2 h1 {
        font-size: 25px;
        line-height: 50px;
        margin: 25px 0;
        color: #fff;
    }
}


.col-2 p {
    color: lightgray;
    font-style: italic;
    font-size: 20px;
}

.col-2 .btn {
    display: inline-block;
    background: #000;
    color: #fff;
    padding: 10px 30px;
    margin: 30px 0;
    border: 1px solid #fff;
    transition: 0.5s ease;
    font-weight: 700;
    cursor: pointer;
}

.col-2 .btn:hover {
    background-color: transparent;
    color: #000;
    background: #fff;
    border: 1px solid #000;
}

/*
======================
Testimonial styling
======================
*/

.testimonial {
    position: relative;
    background: url(/react-sport-store/static/media/testimonial.d8fd6b12.jpg) center/cover no-repeat;
    height: 65vh;
    width: 100%;
    padding: 3rem 0;
    z-index: 1;
    text-align: center;
    z-index: 100;
}

@media screen and (max-width: 1100px){
    .testimonial {
        height: 80vh;
    }
}

@media screen and (max-width: 420px) {
    .testimonial {
        height: 100%;
    }
}

.testimonial::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: -1;
}

.client__image {
    margin-bottom: 3rem;
}

.client__image img {
    width: 5rem;
    height: 5rem;
    max-width: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.testimonial__box {
    width: 100%;
    margin: 0 auto;
    height: 100vh;
    color: #ccc;
}

.testimonial__box p {
    width: 60%;
    margin: auto auto;
    line-height: 1.8rem;
    font-style: italic;
    font-size: 1.5rem;
    margin-bottom: 3rem;
    color: #fff;
    z-index: 100;
}

@media screen and (max-width: 990px){
    .testimonial__box p {
        width: 90%;
    }
}

@media screen and (max-width: 600px) {
    .testimonial__box p {
        width: 90%;
        line-height: 1.5rem;
        font-style: italic;
        font-size: 1.2rem;
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 420px) {
    .testimonial__box p {
        width: 100%;
    }
}

.client__info h3 {
    font-size: 2rem;
    margin-bottom: .5rem;
    color: #fff;
}

.client__info span {
    font-size: 1rem;
    color: orangered;
    font-weight: 500;
}

.swiper-container-horizontal>.swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
    bottom: -50px;
    left: 0;
    width: 100%;
}



